@import url(https://fonts.googleapis.com/css?family=Bitter:400,400i,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.calendar {
    display: flex;
    flex-direction: column;
    width: 80%;
    margin: 0 auto;
    font-size: 30px;
    flex-grow: 0;
}

.calendar__header {
    display: flex;
}

.calendar__dayOfWeek {
    width: calc(100% / 7);
    border: 1px solid black;
    margin-left: -1px;
    text-align: center;
    box-sizing: border-box;

}

.calendar__body {
    display: flex;
    flex-wrap: wrap;
    box-sizing: border-box;
}

.calendar__day {
    width: calc(100% / 7);
    border: 1px solid black;
    margin-left: -1px;
    margin-top: -1px;
    text-align: center;
    box-sizing: border-box;
    font-size: 30px;
    display: flex;
    flex-direction: column;
    flex-grow: 0;


}

.calendar__date {

}

.calendar__event {
    display: flex;
    flex-direction: column;
    flex-grow: 0;
    text-align: left;
    height: 200px;
    overflow-y: scroll;
}

.calendar__event::-webkit-scrollbar {
    width: 0px;
    background: transparent; /* Chrome/Safari/Webkit */
}

.calendar__task {
    font-size: 18px;

}


html {
    --font-phone: Segoe UI, sans-serif;
    --font-invitation: Bitter, sans-serif;

    --icon-color: #444444;
    --btn-background: #f1f3f5;
    --font-color: #777777;
    --main-blue: #59b7c2;
    --white: #ffffff;
    --black: #000000;
    --border: #f7f7f7;
    --gray: #aaaaaa;
    --light-blue: #f5f7f9;
    --light-gray: #cccccc;
    --light-black: #222222;
    --hover-blue: #4698a2;
    --span-background: #e6e9ed;
}

.pop-up__wrapper {
    height: 100vh;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    justify-content: center;
    align-items: center;
    display: flex;
    position: fixed;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
}

.pop-up {
    width: 400px;
    height: 300px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    background: #f5f7f9;
    background: var(--light-blue);
    border: 2px solid #59b7c2;
    border: 2px solid var(--main-blue);
    font-family: Segoe UI, sans-serif;
    font-family: var(--font-phone);
    padding: 20px;

}

.pop-up__title {
    font-family: Bitter, sans-serif;
    font-family: var(--font-invitation);
}

.pop-up__description {
    display: block;
    width: 90%;
    height: 30px;
    margin: 15px 0 15px 0;
    outline: none;
    box-sizing: border-box;
    border: none;
    padding-left: 10px;
    color: #000000;
    color: var(--black);
    font-family: Segoe UI, sans-serif;
    font-family: var(--font-phone);
    font-size: 16px;
    font-weight: 400;
    letter-spacing: 0.3px;
}

.pop-up__description:focus {
    border: 1px solid #59b7c2;
    border: 1px solid var(--main-blue);
    outline: none;
    -webkit-transition: 0.2s;
    transition: 0.2s;
    background-color: #ffffff;
    background-color: var(--white);
}

.pop-up__button-save {
    padding: 10px;
    border: none;
    background: #59b7c2;
    background: var(--main-blue);
}

.pop-up__button-cancel {
    padding: 10px;
    border: none;
}

.pop-up__buttons {
    display: flex;
    margin: 20px 0 30px 0;
    justify-content: space-between;
    width: 50%;
}

.pop-up__button:focus {
    opacity: 0.9;
}

.pop-up__upper {
    width: 90%;
    margin: 0 auto;
}
